import { Fragment, useEffect } from "react";
import "./Orders.css";
import {
  Button
} from "@material-ui/core";
import { DataGrid } from "@material-ui/data-grid";
import { useAlert } from "react-alert";
import { MdDelete, MdEdit } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import {
    clearErrors,
  deleteOrder,
  getAllOrders,
} from "../../../../redux/actions/orderAction";
import { DELETE_ORDER_RESET } from "../../../../redux/constants/orderConstant";

import { adminAuth, userAuth } from "../../../../util/userAuth";
import Loader from "../../../layout/Loader/Loader";
import MetaData from "../../../layout/MetaData/MetaData";
import SideBar from "../Dashboard/SideBar/SideBar";
import H1 from "../../../ReusableComponent/Heading/H1/H1";

const Orders = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const alert = useAlert();

  const { isAuthenticated, user } = useSelector((state) => state.user);
  const { error, orders } = useSelector((state) => state.allOrders);
  const {
    error: orderDeleteError,
    isDeleted,
    loading,
  } = useSelector((state) => state.order);


  const orderDeleteHandler = (id) => {
    dispatch(deleteOrder(id));
   
  };

  const columns = [
    {
      field: "id",
      headerName: "Order ID",
      flex: 0.5,
      minWidth:180
    },
    {
      field: "status",
      headerName: "Status",
      flex: 0.2,
      minWidth:130,
      cellClassName: (params) => {
        return params.getValue(params.id, "status") === "Delivered"
          ? "greenColor"
          : "redColor";
      },
    },
    {
      field: "noOfItems",
      headerName: "No of Items",
      type: "number",
      minWidth:130,
      flex: 0.2,
    },
    {
      field: "amount",
      headerName: "Amount",
      type: "number",
      minWidth:130,
      flex: 0.2,
    },
    {
      field: "actions",
      headerName: "Actions",
      flex: 0.2,
      minWidth:130,
      type: "number",
      sortable: false,
      renderCell: (params) => {
        return (
          <Fragment>
            <Link to={`/admin/order/${params.getValue(params.id, "id")}`}>
              <MdEdit />
            </Link>

            <Button onClick={()=>orderDeleteHandler(params.getValue(params.id, "id"))}>
              <MdDelete />
            </Button>
      
          </Fragment>
        );
      },
    },
  ];
  const rows = [];
  orders &&
    orders.forEach((order) => {
      rows.push({
        id: order._id,
        noOfItems: order.orderItems.length,
        status: order.orderStatus,
        amount: order.totalPrice,
      });
    });

  useEffect(() => {
    userAuth(isAuthenticated, navigate);
    adminAuth(true, user, navigate, alert);

      if (error) {
        alert.error(error);
        dispatch(clearErrors());
      }
      if (orderDeleteError) {
        alert.error(orderDeleteError);
        dispatch(clearErrors());
      }

      if (isDeleted) {
        alert.success("Order Deleted Successfully");
        dispatch({ type: DELETE_ORDER_RESET });
      }

    dispatch(getAllOrders());
  }, [
    isAuthenticated,
    navigate,
    alert,
    user,
    dispatch,
    error,
    orderDeleteError,
    isDeleted,
  ]);

  return (
    <Fragment>
      <MetaData title="All Orders - Admin" />

      <div className="dashboard">
        <SideBar />
        <div className="productListContainer">
        <H1>All Orders</H1>
         

          {loading ? (
            <Loader />
          ) : (
            <DataGrid
              columns={columns}
              rows={rows}
              pageSize={10}
              disableSelectionOnClick
              className="productListTable"
              autoHeight
            />
          )}
        </div>
      </div>
    </Fragment>
  );
};

export default Orders;
